import React from "react";
import "./user-details.scss";
import { Grid, Typography, Box } from "@mui/material";
import InputMask from "react-input-mask";

export const UserDetails = () => {
  return (
    <>
      <Box className="user-info-box">
        <Typography>Создать логин и пароль</Typography>
        <br />
        <div className="tabs-inside">
          <Grid className="rows">
            <div className="input-label">
              <label htmlFor="">Имя:</label>
            </div>
            <Grid className="input-blocks">
              <input type="text" />
            </Grid>
          </Grid>
          <Grid className="rows">
            <div className="input-label">
              <label htmlFor="">Фамилия:</label>
            </div>
            <Grid className="input-blocks">
              <input type="text" />
            </Grid>
          </Grid>
          <Grid className="rows">
            <div className="input-label">
              <label htmlFor="">Эл. почта:</label>
            </div>
            <Grid className="input-blocks">
              <input type="email" />
            </Grid>
          </Grid>
          <Grid className="rows">
            <div className="input-label">
              <label htmlFor="">Тел. номер:</label>
            </div>
            <Grid className="input-blocks">
              <InputMask mask="+7(999) 999 9999">
                {() => <input type="tel" />}
              </InputMask>
            </Grid>
          </Grid>
          <Grid className="rows">
            <div className="input-label">
              <label htmlFor="">Пароль:</label>
            </div>
            <Grid className="input-blocks">
              <input type="password" />
            </Grid>
          </Grid>
          <Grid className="rows">
            <div className="input-label">
              <label htmlFor="">Повторите пароль:</label>
            </div>
            <Grid className="input-blocks">
              <input type="password" />
            </Grid>
          </Grid>
          <Grid className="rows">
            <Grid className="submit-btn">
              <button>Зарегистрироваться</button>
            </Grid>
          </Grid>
        </div>
      </Box>
    </>
  );
};
