import React, { useState } from "react";
import { Grid, Typography } from "@mui/material";
import logo from "../../common/assets/images/logo-black.svg";
import { Link, useNavigate } from "react-router-dom";
import "./login.scss";
import withServices from "../../hocs/withServices";
import { useForm } from "react-hook-form";

function LoginPage({ services }) {
  let navigate = useNavigate();
  const { register, handleSubmit } = useForm();
  const [activate, setActivate] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");

  const onSubmit = (data) => {
    services.auth
      .login(data)
      .then((response) => {
        // Если почта не подтвержедна, то...
        if (!response) {
          setActivate(true);
          return;
        }

        navigate("/");
      })
      .catch((error) => setErrorMessage(error));
  };

  return (
    <>
      <div className="page-container">
        <form onSubmit={handleSubmit(onSubmit)} noValidate>
          <Link to="/" className="logo-login">
            <img className="logo" src={logo} alt="" />
          </Link>
          {errorMessage && (
            <Typography variant="body1" color="error">
              {errorMessage}
            </Typography>
          )}
          <input
            name="email"
            {...register("email")}
            type="text"
            placeholder="Логин"
          />
          <br />
          <input
            {...register("password")}
            name="password"
            type="password"
            placeholder="Пароль"
          />
          <Link>Забыли пароль?</Link>
          <div className="buttons">
            <button type="submit">Вход</button>
            <Link to="/registration" className="regBtn">
              <button>Регистрация</button>
            </Link>
          </div>
        </form>
      </div>
    </>
  );
}

export default withServices()(LoginPage);
