import React, { useState } from "react";
import {
  Box,
  Stepper,
  Step,
  StepButton,
  Button,
  Typography,
  Grid,
} from "@mui/material";
import "./registration-page.scss";
import { CompanyRegistrationPage } from "../CompanyRegistrationPage/CompanyRegistrationPage";
import { UserDetails } from "../UserDetails/UserDetails";
import { Container } from "@mui/system";

export const RegistrationPage = () => {
  const [activeStep, setActiveStep] = useState(0);
  const [completed, setCompleted] = useState({});

  const steps = [
    "Заполнение данных пользователя",
    "Заполнение бизнес-данных",
    "Пользовательское соглашение",
  ];

  const totalSteps = () => {
    return steps.length;
  };

  const completedSteps = () => {
    return Object.keys(completed).length;
  };

  const isLastStep = () => {
    return activeStep === totalSteps() - 1;
  };

  const allStepsCompleted = () => {
    return completedSteps() === totalSteps();
  };

  const handleNext = () => {
    const newActiveStep =
      isLastStep() && !allStepsCompleted()
        ? // It's the last step, but not all steps have been completed,
          // find the first step that has been completed
          steps.findIndex((step, i) => !(i in completed))
        : activeStep + 1;
    setActiveStep(newActiveStep);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleStep = (step) => () => {
    setActiveStep(step);
  };

  const handleComplete = () => {
    const newCompleted = completed;
    newCompleted[activeStep] = true;
    setCompleted(newCompleted);
    handleNext();
  };

  const handleReset = () => {
    setActiveStep(0);
    setCompleted({});
  };

  return (
    <>
      <Container maxWidth="lg">
        <Grid
          sx={{
            width: "100%",
            textAlign: "center",
            marginTop: "20px",
            marginBottom: "30px",
          }}
        >
          <h3
            style={{ color: "#FCA522", fontSize: "25px", marginBottom: "10px" }}
          >
            Регистрация
          </h3>
          <p>
            Регистрация на портале состоит из трех этапов, пожалуйста, заполните
            все этапы
          </p>
        </Grid>
        <Grid className="registration-box">
          <Box sx={{ width: "100%" }}>
            <Stepper nonLinear activeStep={activeStep}>
              {steps.map((label, index) => (
                <Step key={label} completed={completed[index]}>
                  <StepButton color="inherit" onClick={handleStep(index)}>
                    {label}
                  </StepButton>
                </Step>
              ))}
            </Stepper>
            <div>
              {allStepsCompleted() ? (
                <>
                  <Typography sx={{ mt: 2, mb: 1, textAlign: "center" }}>
                    Все этапы успешно пройдены
                  </Typography>
                  <Box sx={{ display: "flex", flexDirection: "row", pt: 2 }}>
                    <Box sx={{ flex: "1 1 auto" }} />
                    <Button onClick={handleReset}>Перерегистрация</Button>
                  </Box>
                </>
              ) : (
                <>
                  {activeStep === 0 && !completed[activeStep] ? (
                    <>
                      <UserDetails />
                    </>
                  ) : (
                    <>
                      <Typography
                        sx={{
                          width: "100%",
                          textAlign: "center",
                          marginTop: "20px",
                        }}
                      >
                        Вы уже прошли этот этап регистрации{" "}
                      </Typography>
                    </>
                  )}
                  {activeStep === 1 ? (
                    <>
                      <CompanyRegistrationPage />
                    </>
                  ) : activeStep === 2 ? (
                    <></>
                  ) : null}

                  <Box sx={{ display: "flex", flexDirection: "row", pt: 2 }}>
                    <Button
                      color="inherit"
                      disabled={activeStep === 0}
                      onClick={handleBack}
                      sx={{ mr: 1 }}
                    >
                      Назад
                    </Button>
                    <Box sx={{ flex: "1 1 auto" }} />
                    {completed[activeStep] ? (
                      <Button onClick={handleNext} sx={{ mr: 1 }}>
                        Следующий этап
                      </Button>
                    ) : null}

                    {activeStep !== steps.length &&
                      (!completed[activeStep] ? (
                        <Button onClick={handleComplete}>
                          {completedSteps() === totalSteps() - 1
                            ? "Завершить"
                            : "Завершить этап"}
                        </Button>
                      ) : // <Typography
                      //   variant="caption"
                      //   sx={{ display: "inline-block" }}
                      // >
                      //   {activeStep + 1} этап уже завершен
                      // </Typography>
                      null)}
                  </Box>
                </>
              )}
            </div>
          </Box>
        </Grid>
      </Container>
    </>
  );
};
